<!--
 * @Description: 新增文章/编辑文章
 * @Author: icony/精武陈真
 * @Date: 2019-08-07 14:01:40
 * @LastEditTime: 2019-10-11 10:43:14
 * @LastEditors: icony/精武陈真
 -->
<template>
<div>
     <breadcrumb-wrapper>
        <template v-slot:left>
            <el-breadcrumb class="app-breadcrumb" separator="/" >
                <transition-group name="breadcrumb">
                <el-breadcrumb-item key="1" v-if="fromPage=='Article'">
                    <router-link  :to="'/article?id='+sid+'&uid='+uid">我添加的文章</router-link>
                </el-breadcrumb-item>
                <el-breadcrumb-item key="2" v-if="fromPage=='Database'">
                    <router-link  :to="'/database?id='+sid">资料库</router-link>
                </el-breadcrumb-item>database
                <el-breadcrumb-item key="3" v-if="fromPage=='ArticleDetail'">
                    <router-link  :to="'/articleDetail/'+article.id+'?fromPage=Article&sid='+sid">文章详情</router-link>
                </el-breadcrumb-item>
                <el-breadcrumb-item key="4" v-if="fromPage=='DataDetail'">
                    <router-link  :to="'/dataDetail/'+article.id+'?fromPage=Database&sid='+sid">资料详情</router-link>
                </el-breadcrumb-item>
                <el-breadcrumb-item key="5">
                    <span v-if="name=='EditArticle'">编辑文章</span>
                    <span v-if="name=='AddArticle'">添加文章</span>
                    <span v-if="name=='AddData'">添加资料</span>
                    <span v-if="name=='EditData'">编辑资料</span>
                </el-breadcrumb-item>
                </transition-group>
            </el-breadcrumb>
        </template>
         <template v-slot:right>
              <el-button  type='primary' :loading="loading" @click='publish'>
                    发布
                </el-button>
         </template>
    </breadcrumb-wrapper>
     <el-card shadow='hover' >
       <div class="w750">
           <el-input v-model="article.title" maxlength="60"  placeholder="文章标题不超过60个字符"></el-input>
       </div>
       <div class="editorBox w750 ">
           <textarea  id="editor1"></textarea>
           <div class="contentNum">字数统计：{{contentNum}}</div>
           <!-- <smeditor :config='config'></smeditor> -->
       </div>
   </el-card>
</div>

</template>
<script>

//import CKEditor from '@ckeditor/ckeditor5-vue';
import DocumentEditor  from '@ckeditor/ckeditor5-build-decoupled-document';
import { getCookie } from '@/utils/auth'
import { getNavigatorLang } from '@/utils'
import { Message } from 'element-ui';
import BreadcrumbWrapper from '@/layout/BreadcrumbWrapper'

const token = getCookie()
const lang =  getNavigatorLang()
const config = {
  // 接口地址
  uploadUrl: `${process.env.VUE_APP_BASE_API}/user/info/upload?token=${token}&lang=${lang}`,
  // form 里的 filename
  uploadName: 'file',
  // 其他参数
  uploadParams: {},
  sticky: true,
  // 上传成功回调
  uploadCallback: (data) => {
    // console.log(data)
    return data.data.url
  },
  // 上传失败回调, 可选
  uploadFailed: (err) => {
    Message({
        type:'error',
        message: '上传失败'
    })
  }
}


export default {
    name: 'AddEditArticle',
    components: {
        BreadcrumbWrapper
    },
    data() {
        return {
            title: '',
            loading:false,
            name:'',
            uid:this.$store.getters.user.id,
            fromPage:this.$route.query.fromPage,
            sid:'',
            contentNum: 0,
            article:{
                title: '',
                content: '',
                sid: undefined,
            }
        }
    },
    watch: {
        $route: {
            handler: function(route) {
                const { sid } = this.$route.query
                this.article.sid = sid
            },
            immediate: true
        }
    },
    mounted() {
        const name = this.$route.name
        const { sid } = this.$route.query
        this.name = name
        this.sid = sid
        this.ckeditor()
    },
    methods: {
        ckeditor(){
            CKEDITOR.replace( 'editor1', {
                // Define the toolbar: http://docs.ckeditor.com/ckeditor4/docs/#!/guide/dev_toolbar
                // The full preset from CDN which we used as a base provides more features than we need.
                // Also by default it comes with a 3-line toolbar. Here we put all buttons in a single row.
                toolbar: [
                    { name: 'document', items: [ 'Print' ] },
                    { name: 'clipboard', items: [ 'Undo', 'Redo' ] },
                    { name: 'styles', items: [ 'Format', 'Font', 'FontSize' ] },
                    { name: 'basicstyles', items: [ 'Bold', 'Italic', 'Underline', 'Strike', 'RemoveFormat', 'CopyFormatting' ] },
                    { name: 'colors', items: [ 'TextColor', 'BGColor' ] },
                    { name: 'align', items: [ 'JustifyLeft', 'JustifyCenter', 'JustifyRight', 'JustifyBlock' ] },
                    { name: 'links', items: [ 'Link', 'Unlink' ] },
                    { name: 'paragraph', items: [ 'NumberedList', 'BulletedList', '-', 'Outdent', 'Indent', '-', 'Blockquote' ] },
                    { name: 'insert', items: [ 'Image', 'Table' ] },
                    { name: 'tools', items: [ 'Maximize' ] },
                    { name: 'editing', items: [ 'Scayt' ] }
                ],

                // Since we define all configuration options here, let's instruct CKEditor to not load config.js which it does by default.
                // One HTTP request less will result in a faster startup time.
                // For more information check http://docs.ckeditor.com/ckeditor4/docs/#!/api/CKEDITOR.config-cfg-customConfig
                customConfig: '',

                // Sometimes applications that convert HTML to PDF prefer setting image width through attributes instead of CSS styles.
                // For more information check:
                //  - About Advanced Content Filter: http://docs.ckeditor.com/ckeditor4/docs/#!/guide/dev_advanced_content_filter
                //  - About Disallowed Content: http://docs.ckeditor.com/ckeditor4/docs/#!/guide/dev_disallowed_content
                //  - About Allowed Content: http://docs.ckeditor.com/ckeditor4/docs/#!/guide/dev_allowed_content_rules
                disallowedContent: 'img{width,height,float}',
                extraAllowedContent: 'img[width,height,align]',
                fileTools_defaultFileName: 'file',
                fileName:'file',
                image_previewText : ' ',
                // Enabling extra plugins, available in the full-all preset: http://ckeditor.com/presets-all
                extraPlugins: 'tableresize,uploadimage,uploadfile',
                uploadUrl: '`${process.env.VUE_APP_BASE_API}/user/info/upload?token=${token}&lang=${lang}`',
                filebrowserImageUploadUrl : `${process.env.VUE_APP_BASE_API}/user/info/upload?token=${token}&lang=${lang}`,
                /*********************** File management support ***********************/
                // In order to turn on support for file uploads, CKEditor has to be configured to use some server side
                // solution with file upload/management capabilities, like for example CKFinder.
                // For more information see http://docs.ckeditor.com/ckeditor4/docs/#!/guide/dev_ckfinder_integration

                // Uncomment and correct these lines after you setup your local CKFinder instance.
                // filebrowserBrowseUrl: 'http://example.com/ckfinder/ckfinder.html',
                // filebrowserUploadUrl: 'http://example.com/ckfinder/core/connector/php/connector.php?command=QuickUpload&type=Files',
                /*********************** File management support ***********************/

                // Make the editing area bigger than default.
                height: 800,

                // An array of stylesheets to style the WYSIWYG area.
                // Note: it is recommended to keep your own styles in a separate file in order to make future updates painless.
                contentsCss: [ 'https://cdn.ckeditor.com/4.8.0/full-all/contents.css', 'mystyles.css' ],

                // This is optional, but will let us define multiple different styles for multiple editors using the same CSS file.
                bodyClass: 'document-editor',

                // Reduce the list of block elements listed in the Format dropdown to the most commonly used.
                format_tags: 'p;h1;h2;h3;pre',

                // Simplify the Image and Link dialog windows. The "Advanced" tab is not needed in most cases.
                removeDialogTabs: 'image:advanced;link:advanced',

                // Define the list of styles which should be available in the Styles dropdown list.
                // If the "class" attribute is used to style an element, make sure to define the style for the class in "mystyles.css"
                // (and on your website so that it rendered in the same way).
                // Note: by default CKEditor looks for styles.js file. Defining stylesSet inline (as below) stops CKEditor from loading
                // that file, which means one HTTP request less (and a faster startup).
                // For more information see http://docs.ckeditor.com/ckeditor4/docs/#!/guide/dev_styles
                stylesSet: [
                    /* Inline Styles */
                    { name: 'Marker', element: 'span', attributes: { 'class': 'marker' } },
                    { name: 'Cited Work', element: 'cite' },
                    { name: 'Inline Quotation', element: 'q' },

                    /* Object Styles */
                    {
                        name: 'Special Container',
                        element: 'div',
                        styles: {
                            padding: '5px 10px',
                            background: '#eee',
                            border: '1px solid #ccc'
                        }
                    },
                    {
                        name: 'Compact table',
                        element: 'table',
                        attributes: {
                            cellpadding: '5',
                            cellspacing: '0',
                            border: '1',
                            bordercolor: '#ccc'
                        },
                        styles: {
                            'border-collapse': 'collapse'
                        }
                    },
                    { name: 'Borderless Table', element: 'table', styles: { 'border-style': 'hidden', 'background-color': '#E6E6FA' } },
                    { name: 'Square Bulleted List', element: 'ul', styles: { 'list-style-type': 'square' } }
                ]
            } );

            //  CKEditor 初始化完成加载数据
              CKEDITOR.on('instanceReady', e => {
                  this.find()
              })
        
              //  CKEditor 数据改变-监听
              CKEDITOR.instances['editor1'].on('change', e => {
                this.contentNum =  this.getText().length
              })

              CKEDITOR.instances['editor1'].on( 'fileUploadRequest', function( evt ) {
                var fileLoader = evt.data.fileLoader,
                    formData = new FormData(),
                    xhr = fileLoader.xhr;

                xhr.open( 'post', fileLoader.uploadUrl, true );
                formData.append( 'file', fileLoader.file, fileLoader.fileName );
                fileLoader.xhr.send( formData );

                // Prevented the default behavior.
                evt.stop();
              }, null, null, 4 );

              CKEDITOR.instances['editor1'].on( 'fileUploadResponse', function( evt ) {
                // Prevent the default response handler.
                evt.stop();
                // Get XHR and response.
                var data = evt.data,
                    xhr = data.fileLoader.xhr,
                    response = JSON.parse(xhr.responseText);

                    if( response.code === '200' ){
                        data.message = response.message;
                        evt.cancel();
                    } else {
                        data.url = response.data.url;
                    }
             } );

        },
        setContent (content) {
            CKEDITOR.instances['editor1'].setData(content)
        },
        getContent () {
           return CKEDITOR.instances['editor1'].getData();
        },
        getText () {
           return CKEDITOR.instances['editor1'].document.getBody().getText().trim();
        },
        publish(){
            const len = this.article.title.length
            const sid = this.sid
            if( len > 0 ){
                const name = this.name;
                const text = this.getText().length
                const content = this.getContent()
                if( text > 0 ){
                    if(name == 'EditArticle'){
                        this.article.content = content
                        this.loading = true
                        this.$store.dispatch('article/articleModify',this.article).then((result)=>{
                            this.loading = false
                            this.$router.push('/article?id='+this.article.subject_id+'&uid='+this.uid)
                            this.$message({
                                message: '修改文章成功',
                                type: 'success'
                            })
                        }).catch(()=>{
                            this.loading = false
                        })
                    }

                    if(name == 'AddArticle'){
                        this.article.content = content
                        this.article.sid = this.sid
                        this.loading = true
                        this.$store.dispatch('article/articleAdd',this.article).then((result)=>{
                            this.loading = false
                            this.$router.push('/article?id='+sid + '&uid='+this.uid)
                            this.$message({
                                message: '发布文章成功',
                                type: 'success'
                            })
                        }).catch(()=>{
                            this.loading = false
                        })
                    }

                    if(name == 'AddData'){
                        this.article.content = content
                        this.article.sid = this.sid
                        this.loading = true
                        this.$store.dispatch('article/materialAdd',this.article).then((result)=>{
                            this.loading = false
                            this.$router.push('/database?id='+sid)
                            this.$message({
                                message: '发布资料成功',
                                type: 'success'
                            })
                        }).catch(()=>{
                            this.loading = false
                        })
                    }

                    if(name == 'EditData'){
                        this.article.content = content
                        this.loading = true
                        this.$store.dispatch('article/materialModify',this.article).then((result)=>{
                            this.loading = false
                            this.$router.push('/database?id='+this.article.subject_id)
                            this.$message({
                                message: '修改资料成功',
                                type: 'success'
                            })
                        }).catch(()=>{
                            this.loading = false
                        })
                    }
                } else {
                    this.$message({
                        message: '内容不能为空！',
                        type: 'error'
                    });
                }
                

            }else{
                this.$message({
                    message: '标题不能为空！',
                    type: 'error'
                });
            }

        },
        find(){
            const { id } = this.$route.params
            if(this.name == 'EditArticle' && id){
                this.$store.dispatch('article/articleGet',{id}).then((result)=>{
                    const {content} = result.data
                    this.article = result.data;
                    this.setContent(content)
                }).catch((err)=>{
                    console.error(err)
                })
            }

            if(this.name == 'EditData' && id){
                this.$store.dispatch('article/materialGet',{id}).then((result)=>{
                    const {content} = result.data
                    this.article = result.data;
                    this.setContent(content)
                }).catch((err)=>{
                    console.error(err)
                })
            }
        }

    },
}
</script>
<style lang="scss" scoped>
 @import "~@/styles/variables.scss";
 .article-header {
     border-bottom: 1px solid $border-2;
     padding-bottom:20px
 }
 .fs12 {
     color: $placeholderText;
     font-size: 12px;
     margin-left: 10px;
 }

 .article-content {
     line-height: 1.8;
     margin-top:30px;
 }

 .contentNum {
     margin-top: 10px;
     font-weight: 400;
     font-size: 14px;
 }



 .editorBox {
     margin-top:20px;
     /deep/ .smeditor {
         width: 100%;
          .preview {
                display: none

            }
         .input-area {
             width: 100%;
             box-shadow: none;
             border:1px solid rgba(240,240,240, 1);
         }
     }

 }

</style>
